import { Component, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';

import { Chart } from 'chart.js';

import { SharedService } from '../../../../../shared/services/shared.service';
import { UserService } from '../../../../.././api/services/user.service';
import { ApartmentService } from '../../../../../api/services/apartment.service';
import { ModalService } from '../../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';


@Component({
  selector: 'app-unit-chart',
  templateUrl: './unit-chart.component.html',
  styleUrls: ['./unit-chart.component.scss']
})
export class UnitChartComponent implements OnInit {
   
  totalUnits:any;

  chart: any;
  @ViewChild('unitPieChart', {static: false}) private chartRef;

  constructor(
  	private userService:UserService, 
  	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  ngOnInit() {

  	this.apartmentService.getApartmentBlockUnitByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res : any) => {

  		this.totalUnits = res.length;

  	},
    error => {
      console.log(error);
    }
    );

  }

  ngAfterViewInit(){

  	
  	setTimeout(()=> {

  		var data = {
		  labels: [
		    "Total Units"
		  ],
		  datasets: [
		    {
		      data: [5],
		      backgroundColor: [
		        "#52cc8e",
		        "#3f51b5"
		      ],
		      hoverBackgroundColor: [
		        "#FF6384",
		        "#36A2EB",
		        "#FFCE56"
		      ]
		 }]
	};



  	this.chart = new Chart(this.chartRef.nativeElement, {
  	 	  type: 'doughnut',
		  data: data,
		  options: {
		  	responsive: true,
		    legend: {
		      display: false
		    },
		    cutoutPercentage: 80,
            rotation: 120,
		    animation: {
                onComplete: (e) => {
                
                var width = this.chart.width,
		        height = this.chart.height,
		        ctx = this.chart.ctx;

			    ctx.restore();

			    ctx.font = "2em Maven Pro";
			    ctx.textBaseline = "middle";
			    ctx.fillStyle = "#4c4c4c";

			    var numText = this.totalUnits,
			    textX = Math.round((width - ctx.measureText(numText).width) / 2),
			    textY = (height / 2) - 10;

			    ctx.fillText(numText, textX, textY);
			    ctx.save();

			    ctx.font = "1.6em Maven Pro";
			    ctx.textBaseline = "middle";
			    ctx.fillStyle = "#4c4c4c";

			    var fText = "Total Units";
			    textX = Math.round((width - ctx.measureText(fText).width) / 2),
			    textY = (height / 2) + 15;

			    ctx.fillText(fText, textX, textY);
			    ctx.save();

                }
            }
		}
  	});

  	}, 500);
  	

  }

}
