import { Component, OnInit } from '@angular/core';

import { SharedService } from '../../../../../shared/services/shared.service';
import { UserService } from '../../../../.././api/services/user.service';
import { ApartmentService } from '../../../../../api/services/apartment.service';
import { ModalService } from '../../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-users-chart',
  templateUrl: './users-chart.component.html',
  styleUrls: ['./users-chart.component.scss']
})
export class UsersChartComponent implements OnInit {

  	
  totalUsers:any;
  totalUnapprovedUsers:any;
  totalApprovedUsers:any;

  totalUnits:any;

  constructor(
  	private userService:UserService, 
  	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }
  

  ngOnInit() {

  	this.userService.getAllUsers().subscribe((res : any) => {

  		this.totalUsers = res.length;

  		this.userService.getAllUnApprovedUsers().subscribe((res : any) => {

  			this.totalUnapprovedUsers = res.length;

	  	},
	    error => {
	      console.log(error);
	    });

  	},
    error => {
      console.log(error);
    }
    );

  	this.totalApprovedUsers = this.totalUsers - this.totalUnapprovedUsers;

  	this.apartmentService.getApartmentBlockUnitByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res : any) => {

  		this.totalUnits = res.length;

  	},
    error => {
      console.log(error);
    }
    );

  }

}
